@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2d383f;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

:root {
  --swiper-theme-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.disabled {
  opacity: 0.1;
  pointer-events: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 1200px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    width: 100%;
    padding: 0 5.86%;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 4.7%;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #0266c1;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
  user-select: none;
}
.btn:hover, .btn:focus-visible {
  outline: none;
  color: #1c3374;
  background-color: transparent;
  border: 1px solid #1c3374;
}
.btn:active {
  background-color: #1c3374;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  min-height: 62px;
  margin: 0 auto;
  color: #ffffff;
  z-index: 3;
}
.header .header__wrapper {
  padding-top: 40px;
}
.header .container {
  position: relative;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .header {
    min-height: 54px;
  }
  .header .header__wrapper {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 44px;
  }
  .header .container {
    flex-wrap: wrap;
    column-gap: 140px;
    align-items: center;
  }
  .header .header__wrapper {
    padding-top: 15px;
  }
}
@media (max-width: 579px) {
  .header .container {
    align-items: start;
  }
}

.header__tel {
  display: flex;
  align-items: center;
  min-width: 142px;
  height: 62px;
  font-size: 18px;
  font-weight: 600;
  line-height: 100%;
  margin-left: 30px;
}
.header__tel:hover, .header__tel:focus-visible {
  outline: none;
  color: #1c3374;
}
.header__tel:active {
  color: #0266c1;
}
@media (max-width: 1199px) {
  .header__tel {
    min-width: 126px;
    height: 44px;
    font-size: 16px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .header__tel {
    min-width: 110px;
    height: 14px;
    font-size: 14px;
    margin-left: auto;
  }
}

@media (max-width: 1199px) {
  .header.open-menu {
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }
  .header.open-menu .header__wrapper {
    background-color: #ffffff;
  }
  .header.open-menu ul {
    display: block;
  }
  .header.open-menu a {
    color: #2d383f;
  }
  .header.open-menu .logo {
    margin-left: 0;
    margin-right: 7px;
  }
  .header.open-menu .menu__toggle::after {
    position: absolute;
    content: "";
    top: 11px;
    left: 0;
    width: 22px;
    height: 22px;
    background-image: url("../img/svg/menu-close.svg");
  }
}
@media (max-width: 767px) {
  .header.open-menu .container {
    column-gap: 0;
  }
  .header.open-menu .logo {
    height: fit-content;
    margin-right: 0;
  }
  .header.open-menu .header__tel {
    position: absolute;
    top: 42px;
    right: 4.7%;
  }
  .header.open-menu .menu__toggle {
    height: 54px;
  }
  .header.open-menu .menu__toggle::after {
    top: 0;
  }
}

@media (max-width: 1199px) {
  .header.no-js ul {
    margin-top: 16px;
  }
  .header.no-js .menu__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .header.no-js ul {
    margin-top: 8px;
  }
}

.hero {
  position: relative;
}

.hero__swiper {
  position: relative;
}
.hero__swiper .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  opacity: 1;
  border: 1px solid #ffffff;
  background: none;
}
.hero__swiper .swiper-pagination-bullet-active {
  background: #ffffff;
}
@media (max-width: 767px) {
  .hero__swiper .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
  }
}

.hero__swiper-slide {
  position: relative;
  width: 100%;
  min-height: 800px;
  background-size: 100%;
  background-repeat: no-repeat;
}
.hero__swiper-slide img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.hero__swiper-slide::before {
  position: absolute;
  content: "";
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(143deg, rgba(15, 20, 41, 0.33) 0%, rgba(15, 20, 41, 0) 100%);
  z-index: 1;
}
@media (max-width: 1199px) {
  .hero__swiper-slide {
    min-height: 1024px;
    padding: 0 5.86%;
  }
}
@media (max-width: 767px) {
  .hero__swiper-slide {
    min-height: 568px;
    padding: 0 4.69%;
  }
  .hero__swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.hero__wrapper {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  padding: 182px 0 80px;
}
@media (max-width: 1199px) {
  .hero__wrapper {
    margin: 0 auto;
    padding: 174px 0 90px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    margin: 0 auto;
    padding: 139px 0 40px;
  }
}

.hero__content {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  min-height: 538px;
  color: #ffffff;
  z-index: 1;
}
.hero__content h2 {
  width: 100%;
  margin: 0 0 25px;
  font-size: 77px;
  line-height: 100%;
  font-weight: 800;
  z-index: 1;
}
.hero__content p {
  max-width: 464px;
  margin: 0 0 36px;
  font-size: 17px;
  line-height: 141.2%;
}
.hero__content ul {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: auto 0 0;
  padding: 0;
  list-style: none;
}
.hero__content li {
  position: relative;
  width: fit-content;
  max-width: 185px;
  min-height: 44px;
  padding: 13px 19px 13px 39px;
  border: 1px solid #ffffff;
  font-size: 12px;
  line-height: 133%;
}
.hero__content li::before {
  position: absolute;
  content: "";
  top: 13px;
  left: 20px;
  width: 12px;
  height: 12px;
}
.hero__content li:first-of-type::before {
  background-image: url("../img/svg/moon.svg");
}
.hero__content li:last-of-type::before {
  background-image: url("../img/svg/level.svg");
}
@media (max-width: 1199px) {
  .hero__content {
    align-items: center;
    max-width: 678px;
    text-align: center;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
  .hero__content h2 {
    font-size: 56px;
    margin-bottom: 15px;
  }
  .hero__content p {
    margin-bottom: 25px;
  }
  .hero__content ul {
    display: none;
  }
}
@media (max-width: 767px) {
  .hero__content {
    max-width: 290px;
  }
  .hero__content h2 {
    font-size: 30px;
  }
  .hero__content p {
    font-size: 15px;
    line-height: 133%;
    margin-bottom: 20px;
  }
}

.hero__button {
  width: 220px;
  min-height: 58px;
  padding: 21px 10px;
}
@media (max-width: 1199px) {
  .hero__button {
