}

.hero__video-button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 56px;
  height: 56px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
