}
.hero__video-button:disabled {
  border-color: #0266c1;
  background-color: transparent;
  opacity: 0.2;
}
.hero__video-button:disabled svg {
  fill: #0266c1;
}
.hero__video-button svg {
  position: absolute;
  fill: #ffffff;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.hero__wrapper-photos {
