  }
}
@media (max-width: 767px) {
  .hero__wrapper-photos {
    display: none;
  }
}

.hero__photo-two img {
  right: 0;
  bottom: 0;
  top: auto;
  left: auto;
  width: 277px;
}
@media (max-width: 1199px) {
  .hero__photo-two img {
    width: 236px;
    height: 312px;
  }
}

.hero__wrapper-audio-player {
  position: absolute;
  right: 0;
  top: 119px;
  width: 340px;
  height: 220px;
}
.hero__wrapper-audio-player svg {
  position: absolute;
  top: 0;
  right: 0;
}
.hero__wrapper-audio-player iframe {
  border: none;
  width: 340px;
  height: 220px;
}
@media (max-width: 1199px) {
  .hero__wrapper-audio-player {
    position: relative;
    top: 49px;
