.hero__audio-player-button {
  position: absolute;
  bottom: 42px;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 47px;
  height: 47px;
  border: none;
  background: transparent;
}

.hero__swiper-pagination {
  position: absolute;
  display: flex;
  justify-content: end;
  gap: 5px;
  transition: 0.3s opacity;
  transform: translate3d(0, 0, 0);
  right: 0;
  margin: 0 auto 88px;
  max-width: 1200px;
  z-index: 3;
}
@media (max-width: 1199px) {
  .hero__swiper-pagination {
    justify-content: center;
    margin-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .hero__swiper-pagination {
    justify-content: center;
    margin-bottom: 32px;
  }
}

.menu {
  margin-top: 20px;
}
.menu ul {
  display: flex;
  flex-wrap: wrap;
  gap: 22px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu li {
  margin: 0;
  padding: 0;
}
.menu a {
  display: flex;
  width: fit-content;
  padding: 3px 5px;
  transition: opacity 0.3s ease;
}
.menu a:hover, .menu a:focus-visible {
  border-bottom: 1px solid currentColor;
}
.menu a:active {
  padding: 2px 4px;
  border: 1px solid currentColor;
}

.menu__toggle {
  position: relative;
  display: none;
}
@media (max-width: 1199px) {
  .menu__toggle {
    display: block;
    width: 26px;
    height: 44px;
    border: none;
    background-color: transparent;
  }
  .menu__toggle::after {
    position: absolute;
    content: "";
    top: 9px;
    left: 0;
    width: 26px;
    height: 26px;
    background-image: url("../img/svg/menu-open.svg");
  }
}
@media (max-width: 767px) {
  .menu__toggle {
    height: 42px;
  }
  .menu__toggle::after {
    top: 0;
  }
}

@media (max-width: 1199px) {
  .menu.menu-footer {
    margin-top: 4px;
  }
  .menu.menu-footer ul {
    gap: 11px;
  }
}
@media (max-width: 767px) {
  .menu.menu-footer {
    margin-top: 13px;
  }
  .menu.menu-footer ul {
    display: block;
    column-count: 2;
    column-gap: 20px;
  }
  .menu.menu-footer li {
    margin-bottom: 7px;
  }
}

@media (max-width: 1199px) {
  .menu-header {
    margin-top: 0;
    display: grid;
    row-gap: 47px;
    order: -1;
  }
  .menu-header ul {
    display: none;
    order: 1;
    padding-bottom: 67px;
  }
  .menu-header li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .menu-header a {
    height: 22px;
    font-weight: 700;
    letter-spacing: 0.64px;
    text-transform: uppercase;
    padding-left: 0;
  }
  .menu-header a:hover, .menu-header a:focus-visible {
    padding: 3px 5px;
    border-bottom: 1px solid currentColor;
  }
  .menu-header a:active {
    padding: 2px 4px;
    border: 1px solid currentColor;
  }
}
@media (max-width: 767px) {
  .menu-header {
    row-gap: 28px;
  }
  .menu-header ul {
    padding-bottom: 46px;
  }
  .menu-header li:not(:last-child) {
    margin-bottom: 16px;
  }
  .menu-header a {
    height: 20px;
    font-size: 14px;
    letter-spacing: 0.56px;
  }
}

