.slider .swiper-button-next,
.slider .swiper-button-prev {
  top: 0;
  right: 0;
  width: 37px;
  height: 35px;
  background-color: #0266c1;
  border: 1px solid #0266c1;
  z-index: 1;
}
.slider .swiper-button-next::after,
.slider .swiper-button-prev::after {
  position: absolute;
  content: "";
  top: 13px;
  left: 12px;
  bottom: 0;
  right: 0;
  background-image: url("../img/svg/arrow-button.svg");
  background-repeat: no-repeat;
}
.slider .swiper-button-next:hover, .slider .swiper-button-next:focus-visible,
.slider .swiper-button-prev:hover,
.slider .swiper-button-prev:focus-visible {
  outline: none;
  background-color: transparent;
  border-color: #1c3374;
}
.slider .swiper-button-next:hover::after, .slider .swiper-button-next:focus-visible::after,
.slider .swiper-button-prev:hover::after,
.slider .swiper-button-prev:focus-visible::after {
  background-image: url("../img/svg/arrow-button-hover.svg");
